.about-me{
  background-color: #E3ECF0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 100vw;
  padding: 2em 0;
}
.about-me-header{
  font-size: 2.5em;
  font-family: 'Maven Pro', sans-serif;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-weight: bold;
}
.about-me-profile{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 20%;
}
.about-me-description{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  width: 80%;
}
.about-me-description *{
  text-align: left;
  padding: 0 1.5em;
}
.about-me-description p{
  font-size: 1.5em;
}
.about-me-profile-img{
  width: auto;
  height: auto;
  max-width: 100%;
}
@media screen and (max-width: 976px){
  .about-me-profile{
    width: 100%;
    padding: 2em 0;
  }
  .about-me-description{
    width: 100%;
  }
}



