.App {
  text-align: center;
  font-family: 'Montserrat', sans-serif;
  overflow-x: hidden;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}
.App p{
  font-family: 'Montserrat', sans-serif;
  font-size: 1.2em;
}
.well-text{
  font-size: 80% !important;
  color: #7A7A7A;
}
.icon-img{
  max-height: 250px;
}
.circle{
  border-radius: 50%;
  -webkit-box-shadow: 1px 3px 15px black;
          box-shadow: 1px 3px 15px black;
}
.overlay-blue{
  height: inherit;
  width: 100%;
  background: rgba(0, 128, 255, 0.3);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column wrap;
      flex-flow: column wrap;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.fadeInLeft{
  position: relative;
  -webkit-animation: fadeInLeft 2s;
          animation: fadeInLeft 2s;
}
.fadeInRight{
  position: relative;
  -webkit-animation: fadeInRight 2s;
          animation: fadeInRight 2s;
}
.bounceUp{
  position: relative;
  -webkit-animation: bounceUp 1.5s infinite;
          animation: bounceUp 1.5s infinite;
}
.fadeInDown{
  position: relative;
  overflow-y: hidden;
  -webkit-animation: fadeInDown 3s;
          animation: fadeInDown 3s;
}
.fadeInUpZoom{
  position: relative;
  -webkit-animation: fadeInUpZoom 2.5s;
          animation: fadeInUpZoom 2.5s;
}
.zoomInDelay{
  position: relative;
  -webkit-animation: zoomInDelay 3s;
          animation: zoomInDelay 3s;
}
.zoomIn{
  position: relative;
  -webkit-animation: zoomInDelay 1.5s;
          animation: zoomInDelay 1.5s;
}
.zoomOut{
  position: relative;
  -webkit-animation: zoomInDelay 1.5s reverse;
          animation: zoomInDelay 1.5s reverse;
}
.fadeInDownZoom{
  position: relative;
  -webkit-animation: fadeInDownZoom 1.5s;
          animation: fadeInDownZoom 1.5s;
}
.fadeOutUpZoom{
  position: relative;
  -webkit-animation: fadeOutUpZoom 1.5s;
          animation: fadeOutUpZoom 1.5s;
}
@-webkit-keyframes fadeInLeft{
  from{
    left: -300px;
    opacity: 0;
  }
  to{
    left: 0;
    opacity: 1;
  }
}
@keyframes fadeInLeft{
  from{
    left: -300px;
    opacity: 0;
  }
  to{
    left: 0;
    opacity: 1;
  }
}
@-webkit-keyframes fadeInRight{
  from{
    right: -300px;
    opacity: 0;
  }
  to{
    right: 0;
    opacity: 1;
  }
}
@keyframes fadeInRight{
  from{
    right: -300px;
    opacity: 0;
  }
  to{
    right: 0;
    opacity: 1;
  }
}
@-webkit-keyframes bounceUp{
  0%{
    -webkit-transform: translateY(-100px) scaleY(1);
            transform: translateY(-100px) scaleY(1);
  }
  30%{
    -webkit-transform: translateY(0) scaleY(1);
            transform: translateY(0) scaleY(1);
  }
  50%{
    -webkit-transform: translateY(10px) scaleY(0.5);
            transform: translateY(10px) scaleY(0.5);
  }
  70%{
    -webkit-transform: translateY(0) scaleY(1);
            transform: translateY(0) scaleY(1);
  }
  100%{
    -webkit-transform: translateY(-100px) scaleY(1);
            transform: translateY(-100px) scaleY(1);
  }
}
@keyframes bounceUp{
  0%{
    -webkit-transform: translateY(-100px) scaleY(1);
            transform: translateY(-100px) scaleY(1);
  }
  30%{
    -webkit-transform: translateY(0) scaleY(1);
            transform: translateY(0) scaleY(1);
  }
  50%{
    -webkit-transform: translateY(10px) scaleY(0.5);
            transform: translateY(10px) scaleY(0.5);
  }
  70%{
    -webkit-transform: translateY(0) scaleY(1);
            transform: translateY(0) scaleY(1);
  }
  100%{
    -webkit-transform: translateY(-100px) scaleY(1);
            transform: translateY(-100px) scaleY(1);
  }
}
@-webkit-keyframes fadeInDown{
  from{
    bottom: 800px;
    opacity: 0;
  }
  to{
    bottom: 0;
    opacity: 1;
  }
}
@keyframes fadeInDown{
  from{
    bottom: 800px;
    opacity: 0;
  }
  to{
    bottom: 0;
    opacity: 1;
  }
}
@-webkit-keyframes fadeInUpZoom{
  from{
    -webkit-transform: perspective(300px) translate3d(0, 0, 25px) scaleY(0.9);
            transform: perspective(300px) translate3d(0, 0, 25px) scaleY(0.9);
    opacity: 0;
  }
  to{
    -webkit-transform: perspective(300px) translate3d(0, 0, 25px) scaleY(0.9);
            transform: perspective(300px) translate3d(0, 0, 25px) scaleY(0.9);
    opacity: 0.9;
  }
}
@keyframes fadeInUpZoom{
  from{
    -webkit-transform: perspective(300px) translate3d(0, 0, 25px) scaleY(0.9);
            transform: perspective(300px) translate3d(0, 0, 25px) scaleY(0.9);
    opacity: 0;
  }
  to{
    -webkit-transform: perspective(300px) translate3d(0, 0, 25px) scaleY(0.9);
            transform: perspective(300px) translate3d(0, 0, 25px) scaleY(0.9);
    opacity: 0.9;
  }
}
@-webkit-keyframes zoomInDelay{
  0%{
    display: none;
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
  }
  75%{
    display: flex;
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
    opacity: 0.5;
  }
  100%{
    opacity: 1;
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
  }
}
@keyframes zoomInDelay{
  0%{
    display: none;
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
  }
  75%{
    display: -ms-flexbox;
    display: flex;
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
    opacity: 0.5;
  }
  100%{
    opacity: 1;
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
  }
}
@-webkit-keyframes fadeInDownZoom {
  0%{
    opacity: 0;
    -webkit-transform: perspective(300px) translate3d(0, -300px, -200px) scaleY(0.2);
            transform: perspective(300px) translate3d(0, -300px, -200px) scaleY(0.2);
  }
  100%{
    opacity: 0.9;
    -webkit-transform: perspective(300px) translate3d(0, 0, 25px) scaleY(0.9);
            transform: perspective(300px) translate3d(0, 0, 25px) scaleY(0.9);
  }
}
@keyframes fadeInDownZoom {
  0%{
    opacity: 0;
    -webkit-transform: perspective(300px) translate3d(0, -300px, -200px) scaleY(0.2);
            transform: perspective(300px) translate3d(0, -300px, -200px) scaleY(0.2);
  }
  100%{
    opacity: 0.9;
    -webkit-transform: perspective(300px) translate3d(0, 0, 25px) scaleY(0.9);
            transform: perspective(300px) translate3d(0, 0, 25px) scaleY(0.9);
  }
}
@-webkit-keyframes fadeOutUpZoom{
  0%{
    opacity: 0.9;
    -webkit-transform: perspective(300px) translate3d(0, 0, 25px) scaleY(0.9);
            transform: perspective(300px) translate3d(0, 0, 25px) scaleY(0.9);
    display: flex;
  }
  100%{
    opacity: 0;
    -webkit-transform: perspective(300px) translate3d(0, -300px, -200px) scaleY(0.2);
            transform: perspective(300px) translate3d(0, -300px, -200px) scaleY(0.2);
    display: flex;
  }
}
@keyframes fadeOutUpZoom{
  0%{
    opacity: 0.9;
    -webkit-transform: perspective(300px) translate3d(0, 0, 25px) scaleY(0.9);
            transform: perspective(300px) translate3d(0, 0, 25px) scaleY(0.9);
    display: -ms-flexbox;
    display: flex;
  }
  100%{
    opacity: 0;
    -webkit-transform: perspective(300px) translate3d(0, -300px, -200px) scaleY(0.2);
            transform: perspective(300px) translate3d(0, -300px, -200px) scaleY(0.2);
    display: -ms-flexbox;
    display: flex;
  }
}
@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



