.contact-row{
  background-color: #000;
  color: #FFF;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-align: center;
      align-items: center;
  padding: 2em 0;
  width: 100%;
}
.contact-item{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 30%;
}
.contact-item-link{
  color: #FFF;
  width: 2.5em;
}
.contact-item-logo{
  font-size: 2.5em;
  margin: 0;
}
.contact-item-logo:hover{
  color: #00AAFF;
}
.contact-item-logo:hover < .contact-item-border{
  color: blue;
}
.contact-item-border{
  border: 2px solid white;
  padding: 0.5em;
}
.contact-item-border:hover{
  -webkit-transform: rotate(45deg) scale(1.1);
      -ms-transform: rotate(45deg) scale(1.1);
          transform: rotate(45deg) scale(1.1);
}
.contact-item-border:hover .contact-item-link, .contact-item-border:hover .contact-item-logo{
  -webkit-transform: rotate(-45deg) scale(1.3);
      -ms-transform: rotate(-45deg) scale(1.3);
          transform: rotate(-45deg) scale(1.3);
}
.contact-header{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-align: center;
      align-items: center;
  margin-bottom: 1em;
  color: #FFF;
}
.contact-header-title{
  font-family: 'Maven Pro', sans-serif;
  font-size: 2em !important;
  font-weight: bold;
  margin-bottom: 0.5em;
}
.contact-header p{
  margin: 0.5em !important;
  font-size: 1em !important;
}
.contact-header-info{
  text-align: left;
}



